@import "./variables";
@import "./mixins";

.jumbotron.pyn-intro {
  position: relative;
  padding: 0px;
  background-color: rgba(243, 237, 215, 0.5);
}

.bg-pattern {
  background: url(../images/pattern.svg) repeat center;
}

.cacto {
  background: url(../images/cacto.svg) no-repeat left center;
  background-position-y: 2em;
}

.fixed-bg {
  background: url(../images/fix-right.svg) no-repeat right center;
  min-height: 40vh;
}

.jumbotron.pyn-intro h1 {
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 36px;
  font-weight: 400;
  line-height: 46px;
}

.jumbotron.pyn-intro h2 {
  font-size: 42px;
  font-weight: 400;
  color: $base-red;
  text-align: right;
  margin: 0 10% 0;

  span {
    color: $darker-red;
  }
}

.jumbotron.pyn-intro h3 {
  font-size: 28px;
  font-weight: 600;
  color: $base-green;
  text-align: right;
  margin: 0 10% 0;

  span {
    color: $darker-red;
  }
}

.jumbotron.pyn-intro .pyn--intro-logo {
  margin: 5rem 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.jumbotron.pyn-intro .pyn--intro-logo img {
  float: none;
  margin: 0 auto;
  width: 80%;
}

.jumbotron.pyn-intro .container {
  position: relative;
  z-index: 10;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;

  &:after {
    clear: both;
  }

  &:after,
  &:before {
    content: "";
    display: table;
    box-sizing: border-box;
  }
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::selection {
  background: transparentize($dark-red, 0.6);
}

body {
  margin: 0;
  font-family: $default-font-family;
}

a {
  text-decoration: none;
  color: $base-red;
  font-weight: 600;
}

.is-button {
  display: inline-block;
  padding: 8px 18px;
  border-radius: 4px;
  font-weight: 600;
  line-height: 1.4;
  text-transform: uppercase;
  border: solid 2px $base-red;

  &.get-tickets {
    background: $base-red;
    color: $white;

    &:hover {
      background: darken($base-red, 5%);
    }
  }

  &.submissions {
    background: transparent;
    color: $base-red;

    &:hover {
      color: $dark-red;
      background: $light-red;
    }
  }
}

.navbar {
  background: $white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px $base-horizontal-padding;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);

  .logo > img {
    height: 32px;
  }

  nav.menu {
    font-size: 16px;

    a {
      margin-left: 15px;

      &.get-tickets {
        color: $base-green;
        font-weight: 600;
        text-transform: uppercase;
      }

      &.submissions {
        text-transform: uppercase;
      }
    }
  }

  .mobile-icon {
    display: none;
    flex-flow: column nowrap;
    justify-content: space-between;
    width: 32px;
    height: 21px;
    cursor: pointer;

    > i {
      display: block;
      width: 100%;
      height: 3px;
      background: $base-red;
    }
  }

  @include tablet-wide {
    padding: 10px 20px;

    nav {
      a {
        font-size: 13px;
        margin-left: 0;
      }
    }
  }

  @include tablet {
    position: relative;
    padding: 10px 0; // definido nos elementos
    z-index: 11;

    .logo {
      padding-left: 15px;
    }

    nav.menu {
      box-shadow: inset 0 2px 8px transparentize($black, 0.9);
      position: absolute;
      top: 100%;
      flex-flow: column nowrap;
      background: $white;
      width: 100%;
      display: none;

      &.is-open {
        display: flex;
      }

      a {
        text-align: center;
        color: $base-red;
        padding: 15px 0px;
        font-size: 18px;

        &.submissions {
          margin: 0 15px 15px;
        }
      }
    }

    .mobile-icon {
      display: flex;
      margin-right: 15px;
      z-index: 50;
    }
  }
}

.footer {
  padding: 25px;
  background-color: $darker-green;
}

.footer p {
  margin: 0;
  color: $beige;
  text-align: center;
  font-size: 14px;
}

.footer-brand {
  height: 50px;
  width: 100%;
  margin: 0 auto 10px;
  background: url("data:image/png;base64,")
    no-repeat center center;
}

/* Hero section */

.hero {
  > .content {
    padding: 50px ($base-horizontal-padding * 3) 90px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    color: $white;

    h1 {
      margin: 20px 0;
      font-size: 80px;
      letter-spacing: -1px;
      color: $light-green;
      line-height: 30px;

      .python {
        color: $dark-red;
        font-weight: 400;
      }

      .nordeste {
        color: $base-red;
        font-weight: 600;
      }
    }

    h2 {
      font-size: 28px;
      font-weight: 500;
    }

    h3 {
      font-size: 26px;
    }
  }

  .more-button {
    cursor: pointer;

    > i {
      display: block;
      position: relative;
      margin: 0 auto 10px;
      width: 25px;
      height: 25px;
      border: solid $white;
      border-width: 0 2px 2px 0;
      transform: rotateZ(45deg);
      animation: arrow 1s ease-in-out;
      animation-iteration-count: 2;
    }

    &:hover {
      i {
        top: 4px;
      }
    }
  }
}

@keyframes arrow {
  0%,
  100% {
    top: 0;
  }

  50% {
    top: 5px;
  }
}

/* About */

.about {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 40% 60%;
  background: $light-green;
  color: $darker-green;

  .section-title {
    display: flex;
    align-items: start;
    justify-content: baseline;
    max-width: 600px;
    padding: 5% 20%;
  }

  .content {
    font-size: 20px;
    padding: 40px 60px;
    max-width: 600px;
    text-align: justify;

    p {
      margin-top: 0;
    }
  }
}

/* Sponsors */

.sponsors {
  background: $white;
  color: $darker-green;

  .subsection {
    text-transform: uppercase;
    font-size: 32px;
    margin: 50px 0;
  }

  .content {
    margin: auto;
    max-width: 450px;
  }
}

/* Locations */

.locations {
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(../images/cais.jpg) 50% 0 no-repeat fixed;
  background-position: center -100px;
  background-size: cover;

  color: $white;

  a {
    color: $white;
    font-weight: bold;
  }
}

/* Tickets CTA */
.tickets-cta {
  background-color: $lighter-red;
  color: $darker-red;

  .content {
    margin: auto;
    max-width: 500px;
  }
}

.map-container {
  position: relative;
  z-index: 5;
  border-radius: 15px;
  box-shadow: 0 10px 60px 0 rgba(0, 0, 0, .74);
}

/* Utilities */

.section-base {
  padding: 50px 0;
  text-align: center;

  .section-title {
    font-weight: 600;
    font-size: 40px;
  }

  .content {
    padding: 0 $base-horizontal-padding;
  }
}

.hidden {
  display: none;
}
